import { Style } from './EditShapes.js';
import UndoStack from './UndoStack.js';
import EditLayer from './EditLayer.js';

const av = Autodesk.Viewing;

export default class Selection {

    constructor(layer, undoStack) {

        av.EventDispatcher.prototype.apply(this);

        // Keys: shapeIds, Values: shapes
        this.isSelected = {};

        // For mouse-over highlighting. 0 = nothing highlighted (ShapeIds start at 1)
        this.hoveredId = 0;

        // Reused as temporary override style for shapes
        this.tmpStyle = new Style();

        // Callback function that controls how to modify the style on selected shapes
        this.modifier = (shape, style) => {

            // only modify style for selected or hovered shapes
            const isSelected = this.isSelected[shape.id];
            const isHovered  = shape.id === this.hoveredId;
            if (!isSelected && !isHovered) {
                return undefined;
            }

            // Create a tmp copy of the initial shape style that is modified to indicate highlighting.
            // Note that we can reuse tmpStyle for multiple shapes, because style modifiers are applied right before drawing.
            this.tmpStyle.copy(style);

            if (isSelected) {
                this.tmpStyle.fillAlpha = Math.min(this.tmpStyle.fillAlpha + 0.3, 1);
                this.tmpStyle.lineWidth *= 1.5;
            } else {
                // hovered
                this.tmpStyle.fillAlpha = Math.min(this.tmpStyle.fillAlpha + 0.2, 1);
                this.tmpStyle.lineWidth *= 1.5;
            }

            return this.tmpStyle;
        };

        this.layer = layer;
        this.onShapeRemoved = this.onShapeRemoved.bind(this);
        this.onLayerCleared = this.onLayerCleared.bind(this);
        this.layer.addEventListener(EditLayer.SHAPE_REMOVED, this.onShapeRemoved);
        this.layer.addEventListener(EditLayer.LAYER_CLEARED, this.onLayerCleared);

        this.layer.addStyleModifier(this.modifier);

        // Make sure that selection doesn't keep deleted objects, e.g., if creation has undone
        // or a RemoveShape action happened.
        this.undoStack = undoStack;
        this.onActionCb = (a) => this.onAction(a);
        this.undoStack.addEventListener(UndoStack.AFTER_ACTION, this.onActionCb);

        this.selectionFilters = [this.defaultSelectionFilter];
    }

    dtor() {
        this.layer.removeStyleModifier(this.modifier);
        this.undoStack.removeEventListener(UndoStack.AFTER_ACTION, this.onActionCb);
        this.undoStack.removeEventListener(EditLayer.SHAPE_REMOVED, this.onShapeRemoved);
        this.undoStack.removeEventListener(EditLayer.LAYER_CLEARED, this.onLayerCleared);
    }

    addSelectionFilter(filter) {
        this.selectionFilters.push(filter);
    }

    removeSelectionFilter(filter) {
        let index = this.selectionFilters.indexOf(filter);
        if (index == -1) {
            return false;
        }
        this.selectionFilters.splice(index, 1);
        return true;
    }

    defaultSelectionFilter(shapes) {
        return shapes.every(s => s.selectable);
    }

    // @param {Shape[]) shapes
    setSelection(shapes) {
        // This works for null as well (unlike ES6 default params)
        shapes = shapes || [];

        // shapes must pass selection filters to get selected.
        if (!this.selectionFilters.every(sf => sf(shapes))) {
            return;
        }

        const selected = this.getSelectedShapes();

        // Check if the set of ids changed
        let changed = false;
        if (selected.length != shapes.length) {
            changed = true;
        } else {
            // Check if any new shape was not selected before
            for (let i=0; i<shapes.length; i++) {
                const shape = shapes[i];
                if (!this.isSelected[shape.id]) {
                    changed = true;
                }
            }
        }

        if (!changed) {
            return;
        }

        // Add all shapes to this.isSelected
        this.isSelected = {};
        for (let i=0; i<shapes.length; i++) {
            const shape = shapes[i];
            this.isSelected[shape.id] = shape;
        }
        this.modified();
    }

    empty() {
        return !Object.keys(this.isSelected).length;
    }

    // Set selection to a single shape. Calling with null clears the selection.
    selectOnly(shape) {
        this.setSelection(shape && [shape]);
    }

    shapeIsSelected(shape) {
        return shape && Boolean(this.isSelected[shape.id]);
    }

    toggleSelection(shape) {
        const wasSelected = Boolean(this.isSelected[shape.id]);
        this.isSelected[shape.id] = wasSelected ? null : shape;

        this.modified();
    }

    clear() {
        this.setSelection([]);
    }

    // Passes all selected shapes to the callback
    getSelectedShapes() {
        return Object.values(this.isSelected).filter(Boolean);
    }

    getSelectedIds() {
        return this.getSelectedShapes().map(s => s.id);
    }

    modified() {
        this.layer.update();
        this.dispatchEvent({type: Selection.Events.SELECTION_CHANGED});
    }

    onAction() {

        if (this.empty()) {
            return;
        }

        // Single-selection: Clear selection if selected shape has gone
        let selected = this.getSelectedShapes();
        if (selected.length == 1) {
            const exists = this.layer.findShapeById(selected[0].id);
            if (!exists) {
                this.clear();
            }
            return;
        }

        // Multi-selection: Same principle, but avoiding n^2 runtime for large selections

        // Create dictionary of all shapes in the layer
        const shapeIdExists = {};
        this.layer.shapes.forEach(s => shapeIdExists[s.id] = true);

        // Clear all shapes from selection that don't exist anymore
        selected = selected.filter(s => shapeIdExists[s.id]);
        this.setSelection(selected);
    }

    onShapeRemoved({shape: shape}) {
        if (this.empty()) {
            return;
        }

        // Remove the shape from the selection if found.
        if (this.isSelected[shape.id]) {
            delete this.isSelected[shape.id];
            this.modified();
        }
    }

    onLayerCleared() {
        if (this.empty()) {
            return;
        }

        this.clear();
    }

    setHoveredId(id) {

        if (id === this.hoveredId) {
            return;
        }

        this.hoveredId = id;
        this.layer.update();
        this.dispatchEvent({type: Selection.Events.SELECTION_HOVER_CHANGED});
    }

    // Result may be null if selection is empty
    // @returns {Shape|null}
    getFirstSelected() {
        const selected = this.getSelectedShapes();
        return selected.length ? selected[0] : null;
    }
}

Selection.Events = {
    SELECTION_CHANGED:       'selectionChanged',
    SELECTION_HOVER_CHANGED: 'selectionHoverChanged'
};
