import { Style, Circle } from '../EditShapes.js';
import { Actions } from '../Actions.js';
import EditToolBase from './EditToolBase';

const InsertSymbolToolName = "Edit2_InsertSymbolTool";

export default class InsertSymbolTool extends EditToolBase {

    constructor(ctx) {
        super(ctx);

        Autodesk.Viewing.EventDispatcher.prototype.apply(this);

        this.symbol = new Circle(0, 0,
            0.2,
            new Style({
                fillAlpha: 1.0,
                lineWidth: 0.01,
                fillColor: 'rgb(255, 255, 0)',
            }));
    }

    getName() {
        return InsertSymbolToolName + this.nameSuffix;
    }

    activate() {}
    deactivate() {}
    register() {}

    setSymbol(symbol) {
        this.symbol = symbol;
    }

    handleMouseMove(event) {
    }

    handleSingleClick(event) {

        const res = this.layer.canvasToLayer(event.canvasX, event.canvasY);
        let symbol = this.symbol.clone();
        symbol.move(res.x, res.y);

        this.undoStack.run(new Actions.AddShape(this.layer, symbol));

        this.dispatchEvent({type: InsertSymbolTool.SYMBOL_INSERTED, symbol: symbol});

        return true;
    }

    handleDoubleClick(/*event , button */) {
        return true;
    }

    getCursor() {
        return 'crosshair';
    }
}

InsertSymbolTool.SYMBOL_INSERTED = "symbolInserted";
