const UndoToolName = 'Edit2_UndoTool';

const av = Autodesk.Viewing;

// Simple tool that triggers undo/redo in response to a keyboard shortcut
export default class UndoTool {

    constructor(undoStack) {
        this.undoStack = undoStack;
    }

    handleKeyDown(event, keyCode) {
        // Undo
        //  - Ctrl + Z (Windows)
        //  - Command + Z (Mac)
        // Redo
        // - Ctrl + Shift + Z (Windows)
        // - Command + Shift + Z (Mac)
        // - Ctrl + Y (Used on Windows but not Mac)

        if ((event.ctrlKey || event.metaKey) && keyCode === av.KeyCode.z) {
            if (event.shiftKey) {
                this.undoStack.redo();
            } else {
                this.undoStack.undo();
            }

            return true;
        }

        if (event.ctrlKey && keyCode === av.KeyCode.y) {
            this.undoStack.redo();
            return true;
        }

        return false;
    };

    // Some paperwork for ToolController
    getName() {
        return UndoToolName;
    }
    getNames() {
        return [this.getName()];
    }
    activate() { }
    deactivate() { }
    register() { }
};
